* {
  box-sizing: border-box;
}

html, body, #root {
  height: 100%;
}

body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  color: #222;
  background: #fff;
}

.container {
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 16px;
}

.section {
  padding: 40px 0;
}